@import '../utils/variables';

.form {
  display: flex;
  flex-direction: column;

  &__content-box {
    gap: 16px;

  }

  & > h4 {
    margin-bottom: 12px;
  }

  &__input {
    margin-bottom: 30px;
    border: 1px solid $ligh-grey;
    border-radius: 16px;
  }

  input:checked +label {
    border: 1px solid $green;
    fill: $green;
    color: $green;
  }

  &__buttons-box {
    display: flex;
    justify-content: space-between;
  }

  &__button-radio {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    min-width: 156px;
    min-height: 100px;
  }

  &__input-email {
    height: 70px;
    padding: 0 25px 0;

    &:focus {
      outline: none;
      border: 1px solid $green;
    }
  }

  &__input-pass {

  }

  &__button {
    display: flex;
    justify-content: center;
    font-weight: 700;
    text-align: center;
    align-items: center;
    color: #fff;
    background-color: #3C9C41;
  }

  &__log {
    display: flex;
    justify-content: center;
    gap: 7px;

    & > h4 {
      font-weight: 400;
    }

    & > a {
      color: #3C9C41;
    }
  }

  &__log-in {
    margin-bottom: 20px;
  }
};
