@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
h1,h4{margin:0}h1{font-size:24px;font-weight:700;line-height:29px;text-align:center}h4{font-size:18px;font-weight:700;line-height:22px;text-align:left}body{margin:0;font-family:'Montserrat', sans-serif;font-weight:400;font-style:normal;font-size:16px;line-height:20px;color:#5b5b5b}

.App{padding:35px 0}.App-logo{max-width:80px;max-height:82px;margin:0 auto 24px}.App-header>h1{margin-bottom:35px}

.form{display:flex;flex-direction:column}.form__content-box{grid-gap:16px;gap:16px}.form>h4{margin-bottom:12px}.form__input{margin-bottom:30px;border:1px solid #c1c1c1;border-radius:16px}.form input:checked+label{border:1px solid #3c9c41;fill:#3c9c41;color:#3c9c41}.form__buttons-box{display:flex;justify-content:space-between}.form__button-radio{display:flex;flex-direction:column;justify-content:center;align-items:center;min-width:156px;min-height:100px}.form__input-email{height:70px;padding:0 25px 0}.form__input-email:focus{outline:none;border:1px solid #3c9c41}.form__button{display:flex;justify-content:center;font-weight:700;text-align:center;align-items:center;color:#fff;background-color:#3C9C41}.form__log{display:flex;justify-content:center;grid-gap:7px;gap:7px}.form__log>h4{font-weight:400}.form__log>a{color:#3C9C41}.form__log-in{margin-bottom:20px}

.container{margin:0 auto;max-width:570px;padding:0 35px}

